<script setup>
import { ref, computed, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Group, NewBadge, InputLabel, InputGroup, Text, Button, Select, Toggle, Tooltip, Textarea, ColorPicker, ProBadge, MultiSelect } from '@components';

const $widget = useWidgets();
const isLocked = computed(() => !formychat_admin_vars.is_premium)

const icon = computed(() => $widget.currentWidget.config?.icon || {});
const form = computed(() => $widget.currentWidget.config?.form || {});
const target = computed(() => $widget.currentWidget.config?.target || {})

const pages = computed(() => $widget.contents.pages ? $widget.contents.pages.map(p => ({ ...p, label: (p.label || 'Untitled Page') })) : [])
import { DotLottieVue } from '@lottiefiles/dotlottie-vue'
const animationPath = formychat_admin_vars.public + '/js/animation.json';


const routeToEdit = () => {
    $widget.states.showSuccess = false
    $widget.currentWidget.isNew = false
    $widget.editWidget($widget.currentWidget)
}

</script>
<template>
    <div class="w-full h-full space-y-8">
        <div>
            <Group title="Triggers" :gap="false" >
                <Toggle class="pt-1" v-model="$widget.currentWidget.is_active">Activate Widget</Toggle>
                <!-- widget is turned on  -->
                <div class="space-y-5 bg-gray-50 p-5" v-if="$widget.currentWidget.is_active">
                    <InputGroup>
                        <Toggle v-model="icon.has_delay"><strong>Time delay</strong></Toggle>
                        <div class="transition" :class="{ 'opacity-60 pointer-events-none': !icon.has_delay }"> 
                            Display after <Text class="w-16 inline mx-2" v-model="icon.delay" type="number" min="0" max="3600"
                                placeholder="3" /> seconds when page is loaded </div>
                    </InputGroup>

                    <InputGroup>
                        <div class="flex items-center gap-2">
                            <Toggle v-model="icon.show_after_scroll"><strong>Page Scroll</strong> </Toggle>
                            <NewBadge />
                        </div>
                        <div class="transition" :class="{ 'opacity-60 pointer-events-none': !icon.show_after_scroll }"> 
                            Display after <Text class="w-16 inline mx-2" v-model="icon.scroll_to" type="number" min="0" max="100"
                                placeholder="25" /> % scrolling on the page </div>
                    </InputGroup>

                    <InputGroup>
                        <div class="flex items-center gap-2">
                            <Toggle v-model="icon.show_on_exit"><strong>Exit Intent</strong> </Toggle>
                            <NewBadge />
                        </div>
                        <div class="transition"> 
                           Display when visitor is about to leave </div>
                    </InputGroup>
                </div>
                <!-- when widget is turned off  -->
                <div v-else
                    class="bg-gray-50 border border-slate-100 rounded-lg py-5 px-6 gap-2 inline-flex items-center justify-start w-fit  text-gray-600">
                    <svg xmlns="http://www.w3.org/2000/svg" class="fill-current  text-yellow-500 w-4" viewBox="0 0 16 16">
                        <path
                            d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2" />
                    </svg>
                   Widget turned off
                </div>
            </Group>
            
        </div>
        <Group>
            <template #title>
                <div class="flex items-center gap-3"> Targets
                    <ProBadge />
                    <NewBadge />
                </div>
            </template>
            <div class="flex items-start gap-5"
                :class="{ 'opacity-70 formychat-locked child:pointer-events-none': isLocked }">
                <InputLabel class="w-40">Exclude pages</InputLabel>
                <InputGroup class="space-y-2">
                    <MultiSelect :options="pages" v-model="target.exclude_pages"
                        :class="{ ' opacity-60 pointer-events-none': target.exclude_all_pages }" />
                    <Toggle v-model="target.exclude_all_pages">Exclude all pages excepts:</Toggle>
                    <MultiSelect :options="pages" v-model="target.exclude_all_pages_except"
                        :class="{ ' opacity-50 pointer-events-none': !target.exclude_all_pages }" />
                </InputGroup>
            </div>
        </Group>
        <transition name="form_slide">
            <div v-if="$widget.states.showSuccess"
                class="absolute -translate-y-8 sm:translate-y-0 w-full h-full left-0 top-0 bg-white z-50">
                <!-- close  -->
                <div
                    class="absolute -top-8 right-0 cursor-pointer w-12 h-12 flex items-center justify-center text-gray-300 hover:text-gray-700 transition">
                    <svg @click.prevent="routeToEdit" xmlns="http://www.w3.org/2000/svg" class="fill-current w-6"
                        viewBox="0 0 16 16">
                        <path
                            d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708" />
                    </svg>
                </div>
                <div class="flex flex-col gap-4 justify-center items-center h-96">
                    <DotLottieVue class="w-72" autoplay loop :src="animationPath" />
                    <h2 class="text-2xl text-gray-700">{{ $widget.currentWidget.isNew ? 'Setup Finished!' : 'Widget Updated!'}} </h2>
                    <div class="text-lg text-gray-500" v-if="$widget.currentWidget.is_active">Now you can use <a
                            href="https://go.wppool.dev/2rc7" target="_blank"
                            class="text-indigo-500 hover:text-indigo-400 transition">FormyChat</a></div>
                    <div class="flex items-center gap-3 justify-center my-6 font-medium">
                        <Button outline @click="routeToEdit" class="text-lg h-12 px-8">Done</Button>
                        <Button v-if="isLocked" a href="https://go.wppool.dev/2rc7" target="_blank"
                            class="text-lg h-12 px-8" color="danger" outline
                            @click="$widget.states.showSuccess = false">Upgrade all features</Button>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
