import { ref, reactive, computed, onMounted, watch } from 'vue'
import { defineStore } from 'pinia'
import { useRoute, useRouter } from 'vue-router'
import API from '@util/API'
import defaultWidget from './defaultWidget'
import { useModal } from './modal'

export const useWidgets = defineStore('widgets', () => {

    // States.
    const states = reactive({
        errors: {},
        showSuccess: false,
        defaultCountryCode: 44
    })

    // Widget Tabs 
    const route = useRoute();
    const router = useRouter();

    const tabs = ref([
        {
            id: 1,
            path: 'whatsapp',
            title: 'WhatsApp',
        },
        {
            id: 2,
            path: 'customize',
            title: 'Customize',
        },
        {
            id: 3,
            path: 'greetings',
            title: 'Greetings',
        },
        {
            id: 4,
            path: 'triggers',
            title: 'Triggers & Targeting',
        },
    ])

    const isValidPhone = (phone = '') => {
        return phone?.match(/^\d{7,15}$/)
    }

    const setError = (field, value = true) => {
        states.errors[field] = value

        setTimeout(() => {
            const target = document.querySelector(`[name="${field}"]`)
            if ( target ) {
                // Scroll to the error field.
                target.scrollIntoView({ behavior: 'smooth', block: 'center' })

                // Focus on the field.
                target.focus()
            }
        } , 0)
        
        return false 
    }

    const setTab = async (tab) => {
        
        // Middleware.
        states.errors = {}
        const config = currentWidget.value.config
        
        if ( isTab('whatsapp') ) {

            if ( config.whatsapp.agent_mode === 'single' ) {
                if ( !isValidPhone(config.whatsapp.number || '') ) {
                    setError('whatsapp_number')
                }
            } else {
                // Check if at least one agent is set.
                if ( !config.whatsapp.agents || config.whatsapp.agents.length === 0 ) {
                    setError('whatsapp_agents')
                }
                // To each.
                config.whatsapp.agents.forEach( (agent, index) => {
                    if ( !isValidPhone( agent.number || '' ) ) {
                        setError(`agent_number_${index}`)
                        return
                    }
                })
            }
           
        }

        if ( isTab('customize') ) {

            // cf7 form configuration 
            if ( config.form.mode === 'cf7' && !config.form.cf7_id ) {
                setError('cf7_id')
            }

            // Gravity
            if ( config.form.mode === 'gravity' && !config.form.gravity_id ) {
                setError('gravity_id')
            }
            // WPForms
            if ( config.form.mode === 'wpforms' && !config.form.wpforms_id ) {
                setError('wpforms_id')
            }

            // forminator
            if ( config.form.mode === 'forminator' && !config.form.forminator_id ) {
                setError('forminator_id')
            }

            // formidable
            if ( config.form.mode === 'formidable' && !config.form.formidable_id ) {
                setError('formidable_id')
            }

            // ninja
            if ( config.form.mode === 'ninja' && !config.form.ninja_id ) {
                setError('ninja_id')
            }

            // If emails are enabeld and no email is set.
            if ( config.email.enabled && !config.email.admin_email && config.email.address == '') {
                setError('email_address')
            }
        }

        // Bail if errors.
        if ( Object.keys(states.errors).length > 0 ) {
            return false
        }

        router.push({ name: 'widget.edit', params: { widgetId: route.params.widgetId }, query: { tab: tab } })

        // Scroll to top.
        setTimeout(() => {
            window.scrollTo({ top: 0, behavior: 'smooth' })
        } , 0)
    }

    const isTab = (tab) => {
        return route.query.tab == tab;
    }

    const currentTab = computed(() => tabs.value.find( tab => tab.path == route.query.tab ) || tabs.value[0] )
    const isTabEmpty = computed( () => !route.query.tab )

    const isTabDone = ( _tab ) => {
        const tabIndex = tabs.value.findIndex( tab => tab.path == _tab );
        const currentIndex = tabs.value.findIndex( tab => tab.path == route.query.tab );
        return tabIndex <= currentIndex;
    }
    const nextTab = () => {
        const currentIndex = tabs.value.findIndex( tab => tab.path == route.query.tab );
        if ( currentIndex < tabs.value.length - 1 ) {
            // find tab name.
            setTab(tabs.value[currentIndex + 1].path)
        } 
    }

    const prevTab = () => {
        const currentIndex = tabs.value.findIndex( tab => tab.path == route.query.tab );
        if ( currentIndex > 0 ) {
            // find tab name.
            setTab(tabs.value[currentIndex - 1].path)
        } 
    }

    // Widgets.
    const widgets = ref([])

    const clone = async ( widget ) => {

        // Clone triggers.
        const response = await API.post(`widget`, widget)

        if ( response.data.success ) {
            widgets.value.push(response.data.data)
            router.push({ name: 'widget.edit', params: { widgetId: response.data.data.id }, query: { tab: 'whatsapp' } })
        }
    }

    const editWidget = widget => {
        setCurrentWidget( widget )

        router.push({ name: 'widget.edit', params: { widgetId: widget.id || 'new'}, query: { tab: 'whatsapp' } })
    }

    const currentWidget = ref( defaultWidget )
    const setCurrentWidget = ( widget ) => {
        currentWidget.value = widget

        const DEFAULT = defaultWidget.config
        const config = { ...widget.config };

        if ( !formychat_admin_vars.is_premium ) {
           
            config.whatsapp.web_version = DEFAULT.whatsapp.web_version;
            config.whatsapp.agent_mode = DEFAULT.whatsapp.agent_mode;

            config.icon.image_url = DEFAULT.icon.image_url;
            config.icon.size = widget.config.icon.size === 'custom' ? DEFAULT.icon.size : widget.config.icon.size;
            config.icon.position = widget.config.icon.position === 'custom' ? DEFAULT.icon.position : widget.config.icon.position;

            config.cta.color = DEFAULT.cta.color;
            config.cta.background_color = DEFAULT.cta.background_color;

            config.form.size = widget.config.form.size === 'custom' ? DEFAULT.form.size : widget.config.form.size;
            config.form.show_country_code_field = DEFAULT.form.show_country_code_field;
            config.form.text_color = DEFAULT.form.text_color;
            config.form.background_color = DEFAULT.form.background_color;

            config.greetings.style = DEFAULT.greetings.style;
            config.greetings.on_click = DEFAULT.greetings.on_click;

            config.greetings.templates.simple.heading_size = widget.config.greetings.templates.simple.heading_size === 'custom' ? DEFAULT.greetings.templates.simple.heading_size : widget.config.greetings.templates.simple.heading_size;
            config.greetings.templates.simple.message_size = widget.config.greetings.templates.simple.message_size === 'custom' ? DEFAULT.greetings.templates.simple.message_size : widget.config.greetings.templates.simple.message_size;
            config.greetings.templates.simple.message_color = widget.config.greetings.templates.simple.message_color === 'custom' ? DEFAULT.greetings.templates.simple.message_color : widget.config.greetings.templates.simple.message_color;
            config.greetings.templates.simple.background_color = widget.config.greetings.templates.simple.background_color === 'custom' ? DEFAULT.greetings.templates.simple.background_color : widget.config.greetings.templates.simple.background_color;

            config.greetings.templates.wave.icon_url = DEFAULT.greetings.templates.wave.icon_url;
            config.greetings.templates.wave.icon_position = DEFAULT.greetings.templates.wave.icon_position;
            config.greetings.templates.wave.heading_size = widget.config.greetings.templates.wave.heading_size === 'custom' ? DEFAULT.greetings.templates.wave.heading_size : widget.config.greetings.templates.wave.heading_size;
            config.greetings.templates.wave.message_size = widget.config.greetings.templates.wave.message_size === 'custom' ? DEFAULT.greetings.templates.wave.message_size : widget.config.greetings.templates.wave.message_size;
            config.greetings.templates.wave.message_color = DEFAULT.greetings.templates.wave.message_color;
            config.greetings.templates.wave.background_color = DEFAULT.greetings.templates.wave.background_color;
            config.greetings.templates.wave.cta_icon_url = DEFAULT.greetings.templates.wave.cta_icon_url;
            config.greetings.templates.wave.cta_text_color = DEFAULT.greetings.templates.wave.cta_text_color;
            config.greetings.templates.wave.cta_background_color = DEFAULT.greetings.templates.wave.cta_background_color;
            config.greetings.templates.wave.cta_heading_color = DEFAULT.greetings.templates.wave.cta_heading_color;
            config.greetings.templates.wave.cta_message_color = DEFAULT.greetings.templates.wave.cta_message_color;
            config.greetings.templates.wave.cta_heading_size = widget.config.greetings.templates.wave.cta_heading_size === 'custom' ? DEFAULT.greetings.templates.wave.cta_heading_size : widget.config.greetings.templates.wave.cta_heading_size;
            config.greetings.templates.wave.cta_message_size = widget.config.greetings.templates.wave.cta_message_size === 'custom' ? DEFAULT.greetings.templates.wave.cta_message_size : widget.config.greetings.templates.wave.cta_message_size;

            // Adjust icon.
            config.icon = {
                ...formychat_admin_vars.data.widget_config.icon,
                ...config.icon,
                ...DEFAULT.icon,
                ...currentWidget.value.config.icon
            }
        }

        // if ( currentWidget.value.config?.icon ) {
        //     currentWidget.value.config.icon = {
        //         ...DEFAULT.icon,
        //         ...currentWidget.value.config.icon
        //     }
        // }

        if ( config.whatsapp && ! config.whatsapp.agent_mode ) {
            config.whatsapp.agent_mode = 'single'
        }

        // Check for existing data first
        // let currentCountryCode = config?.whatsapp?.country_code || null
        // if ( !currentCountryCode && window.formychat_country_code) {
        //     console.log('get', config.whatsapp.country_code);
        //     config.whatsapp.country_code = window.formychat_country_code.toString() || '44';
        //     console.log('set', config.whatsapp.country_code);
        // }

        // Set.
        currentWidget.value = { ...widget, config }
    }

    const setCountryCode = ( country_code ) => {
        currentWidget.value.config.whatsapp.country_code = country_code
    }

    const deleteWidgets = async ( ids = [] ) => {

        return new Promise( async (resolve) => {
            // Convert to array.
        if ( !Array.isArray(ids) ) {
            ids = [ids]
        }

        // If no ids provided, delete current widget.
        if ( ids.length === 0 ) {
            ids = [currentWidget.value.id]
        }

        const selectedWidgets = widgets.value.filter( w => ids.includes(w.id) )

        // Set loading.
        selectedWidgets.forEach( w => w.deleting = true )

        // Delete widget from server.
        const deleted = await API.delete(`widgets`, { data: { id: ids } })

        if ( deleted.data.success ) {
            selectedWidgets.forEach( w => w.deleted = true )
            setTimeout(() => {
                // Splice from widgets.
                widgets.value = widgets.value.filter( w => !ids.includes(w.id) )
                resolve( widgets.value  )
            }, 500)
        } else {
            selectedWidgets.forEach( w => w.deleting = false )
            resolve( widgets.value )
        }

        })
    }

    const saveWidget = async ( widget = null ) => {
        widget = widget || currentWidget.value 
        

        if ( widget.is_saving ) {
            return false
        }

        if ( ! widget.is_active ) {
            let userConsent = await useModal().fire({
                title: 'The widget is currently turned off!',
                message: 'This floating widget is currently turned off, would you like to turn it ON and save to show it on your site?',
                ok: 'Save & Show on my site',
                cancel: 'Just save and keep it off',
            })

            if ( userConsent.ok ) {
                widget.is_active = true
            }

            if ( userConsent.cancel ) {
                widget.is_active = false
            }

            if ( userConsent.close ) {
                return;
            }
        }

        widget.is_loading = true 

        const isNew = widget.id === null || widget.id === undefined || widget.id === 0
        return new Promise( resolve => {

            widget.is_saving = true

            const request = isNew ? API.post('widget', widget) : API.put(`widget/${widget.id}`, widget)

            request.then( response => {
                // console.log('Widget saved', response.data);
                
                if ( isNew ) {
                    currentWidget.value.id = response.data.id
                }

                states.showSuccess = true

                resolve( response.data )
            }).catch( error => {
                // console.error("Error saving widget", error)  
                resolve( null )
            }).finally(() => {
                widget.is_loading = false
                widget.is_saving = false
            })
        })
    }

    const fetchWidgets = async () => {
        states.loading = true
        return new Promise( resolve => {
            
            API.get('widgets').then( response => {
                widgets.value = response.data.map( w => {
                    return { ...w, is_loading: false, is_selected : false }
                })
                // console.log("Widgets fetched");
                resolve( widgets.value)
            }).catch( error => {
                // console.error("Error fetching widgets", error)  
                resolve( widgets.value)
            }).finally(() => {
                states.loading = false  
            })
        })
    }

    // Filters.
    const filters = reactive({
        sortBy: 'created_at',
        sortOrder: 'desc'
    })

    watch([() => filters.sortBy, () => filters.sortOrder], () => {
        // Sort widgets.
        widgets.value.sort((a, b) => {
            if ( filters.sortOrder === 'asc' ) {
                return a[filters.sortBy] > b[filters.sortBy] ? 1 : -1
            } else {
                return a[filters.sortBy] < b[filters.sortBy] ? 1 : -1
            }
        })
        
    }, { deep: true })

    const setSort = (sortBy) => {
        if ( filters.sortBy === sortBy ) {
            filters.sortOrder = filters.sortOrder === 'asc' ? 'desc' : 'asc'
        } else {
            filters.sortBy = sortBy
            filters.sortOrder = 'asc'
        }
    }

    const getWidgets = computed(() => {
        return widgets.value || []
    })

    /**
     * Update widget.
     */
    const update = async ( widget ) => {
        const current = widgets.value.find( w => w.id === widget.id )
        current.statusLoading = true
        await API.put(`widget/${widget.id}`, widget)
        current.statusLoading = false
    }

    // screen 
    const isMobile = ref( window.innerWidth < 640 )

    window.addEventListener('resize', () => {
        isMobile.value = window.innerWidth < 640
    })

    /**
     * Fetch Contents
     */
    const contents = ref({
        countries: [],
        fonts: [],
        pages: [],
        forms: {}
    })
    const fetchContents = async () => {

        let data = (await API.get('contents')).data

        // Format countries.
        contents.value.countries = data.countries?.sort( (a, b) => a.name.localeCompare(b.name) ).map( country => {
            return {
                value: country.code,
                label: `<span>${country.flag} (${country.code})</span> ${country.name}`,
                name: `<span>${country.flag}</span> ${country.code}`
            }
        });

        // console.log('data.fonts', Object.keys(data.fonts), typeof data.fonts);
        
        // Format fonts.
        contents.value.fonts = Object.keys(data.fonts)?.map( (font, index) => {
            return {
                value: font,
                name: data.fonts[font],
                label: `<span style="font-family: ${font}">${data.fonts[font]}</span> <ProBadge/>`,
                locked: index > 10 && ! formychat_admin_vars.is_premium
            }
        });

        // Format pages.
        contents.value.pages = data.pages.map( page => {
            return {
                value: page.ID,
                name: page.post_title,
                label: page.post_title
            }
        });

        // Forms.
        contents.value.forms = data.forms || {}
        // console.log('contents.value.forms', contents.value.forms);
        
    }

    onMounted(fetchContents)

    return {
        tabs,
        currentTab,
        setTab,
        isTab,
        prevTab,
        nextTab,
        isTabEmpty,
        isTabDone,
        states,
        widgets,
        filters,
        getWidgets,
        setCurrentWidget,
        clone,
        editWidget,
        deleteWidgets,
        fetchWidgets,
        saveWidget,
        setSort,
        isLoading: computed(() => states.loading),
        totalWidgets: computed(() => widgets.value.length),
        update,
        isMobile,
        currentWidget,
        setError,
        errors: computed(() => states.errors),
        contents,
        fetchContents,
        setCountryCode,
        isValidPhone
    }
})