<script setup>
import { ref, computed, reactive, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Group, Tabs, InputLabel, InputGroup, IconUploader, Text, Button, Select, Toggle, NewBadge, Textarea, ColorPicker, Spin, MessagePreset } from '@components';
import API from '@util/API'

const state = reactive({})
const $widget = useWidgets();

const errors = computed(() => $widget.errors || {});

const whatsapp = ref($widget.currentWidget.config?.whatsapp || {})
const icon = ref($widget.currentWidget.config?.icon || {})
const form = ref($widget.currentWidget.config?.form || {})
const cta = ref($widget.currentWidget.config?.cta || {})
const email = ref($widget.currentWidget.config?.email || {})

// Forms.
const cf7 = ref($widget.currentWidget.config?.cf7 || {})

const sizes = ref({
    'small': 'Small',
    'medium': 'Medium',
    'large': 'Large',
    'custom': 'Custom',
});

const positions = ref({
    'left': 'Left',
    'right': 'Right',
    'custom': 'Custom',
});

const formychat_admin_vars = window.formychat_admin_vars || {};

const admin_email = formychat_admin_vars.site.admin_email || '';

const allForms = formychat_admin_vars.data.forms || {}; 

// Form Mode.
const setFormMode = mode => {

    const customForms = Object.keys(allForms).filter(mode => mode !== 'formychat');

    // Reset errors.
    customForms.forEach(mode => {
        $widget.setError(mode + 'NotActivated', false)
        $widget.setError(mode + 'NotInstalled', false)
    })

    if (mode === 'formychat') {
        form.value.mode = mode
        loadFields()
        return;
    }

    // Check if the form is installed and activated.
    if (customForms.includes(mode)) {
        if (!formychat_admin_vars.additional[mode].is_installed) {
            return $widget.setError(mode + 'NotInstalled')
        }

        if (!formychat_admin_vars.additional[mode].is_active) {
            return $widget.setError(mode + 'NotActivated')
        }

        // Set form.mode_form_id if not set.
        if (!form.value[mode + '_id']) {
            form.value[mode + '_id'] = $widget.contents.forms[mode][0]?.value || ''
        }
    }

    // Load form fields.
    form.value.mode = mode
    loadFields()
}

const isLocked = computed(() => !formychat_admin_vars.is_premium)

// Activate CF7
const activatePlugin = async (plugin = 'cf7', action = 'install') => {
    state.pluginActivating = action;

    await API.get('action?action=activate_plugin&plugin=' + plugin);

    formychat_admin_vars.additional[plugin].is_active = true
    formychat_admin_vars.additional[plugin].is_installed = true

    setFormMode(plugin)
    await $widget.fetchContents();
    form.value[plugin + '_form_id'] = $widget.contents.forms[plugin].length > 0 ? $widget.contents.forms[plugin][0]?.value : ''
    // Load CF7 form.
    state.pluginActivating = false;
}

const YouTubeId = ref('')

// Forms.
const forms = formychat_admin_vars.data.forms || {}

const toTitleCase = (string) => {
    // To capital case and replace _/- with space.
    return string.replace(/_/g, ' ').replace(/-/g, ' ').replace(/\b\w/g, char => char.toUpperCase());
}

const loadFields = async () => {
    state.loadingFields = true;
    const result = await API.get('form_fields?mode=' + form.value.mode + '&form_id=' + form.value[form.value.mode + '_id'] || '');
    state.fields = result.data.data || {}

    if ( form.value.mode != 'formychat' && ! whatsapp.value[form.value.mode + '_message_template'] ) {
        // console.log('Found empty template for', form.value.mode, state.fields);
        let template = '';
        for (const field in state.fields) {
            template += `${toTitleCase(field)} : {${field}}\n`;
        }
        whatsapp.value[form.value.mode + '_message_template'] = template;
    }
    state.loadingFields = false;
}

// Adjust cf7_id.
onMounted(() => {
    if (cf7.value.form_id) {
        form.value.cf7_id = cf7.value.form_id

        // Reset cf7_id.
        cf7.value.form_id = ''
    }

    loadFields()
})


</script>
