                    <!-- FormyChat  -->

                    <!-- Message Preset  -->
                    <MessagePreset v-if="form.mode === 'formychat'" v-model="whatsapp.message_template"
                        :fields="state.fields" />
                    <MessagePreset v-else v-model="whatsapp[ form.mode +'_message_template']"
                        :fields="state.fields" />
                </InputGroup>
                <!-- Receive leads via email  -->
                <InputGroup v-if="form.mode == 'formychat'">
                    <div class="flex items-center gap-3">
                        <Toggle locked v-model="email.enabled"
                            hints="Enable this option to receive all lead information to your given email address">
                            Receive leads via email</Toggle>
                        <NewBadge />
                    </div>
                </InputGroup>
                <InputGroup v-if="form.mode == 'formychat'" class="space-y-2" :condition="email.enabled || false">
                    <label><input type="checkbox" class="bg-transparent" v-model="email.admin_email"
                            :value="admin_email">Use admin email to receive leads</label>
                    <Text v-if="email.admin_email" type="email" :placeholder="admin_email"
                        class="pointer-events-none" />
                    <Text name="email_address" v-model="email.address" :error="errors.email_address" v-else type="email"
                        placeholder="Enter email address" />
                    <p class="text-sm text-sate-500 py-4 px-6 bg-gray-100 border border-gray-200 rounded"> <strong
                            class="text-semibold">Note:</strong> Please configure any SMTP e.g. <a class="underline"
                            :href="`${formychat_admin_vars.site.url}/wp-admin/plugin-install.php?s=SMTP&tab=search&type=term`"
                            target="_blank">WP Mail SMTP</a> or <a class="underline"
                            :href="`${formychat_admin_vars.site.url}/wp-admin/plugin-install.php?s=SMTP&tab=search&type=term`"
                            target="_blank">FluentSMTP</a> plugin to receive mail. </p>
                </InputGroup>
                <!-- size  -->
                <InputGroup>
                    <InputLabel> Form Size </InputLabel>
                    <Tabs :options="sizes" v-model="form.size" width="85px" class="bg-gray-50" />
                </InputGroup>
                <InputGroup :condition="form.size == 'custom'">
                    <InputLabel> Custom Form Size </InputLabel>
                    <div class="w-full flex items-center justify-between gap-2">
                        <input class="formychat-slider" type="range" min="100" max="1000" v-model="form.size_custom">
                        <span class="w-16">{{ form.size_custom || 300 }}px</span>
                    </div>
                </InputGroup>
                <!-- Form  -->
                <InputGroup>
                    <InputLabel> Form Title </InputLabel>
                    <Text placeholder="Form Title (Ex: Contact via WhatsApp)" v-model="form.title" name="form_title" />
                </InputGroup>
                <InputGroup>
                    <InputLabel hints="Leave blank to disable"> Header Text </InputLabel>
                    <div class="w-full">
                        <Textarea placeholder="Header Text (Ex: Please fill up the fields below)" class="resize-y"
                            name="form_subtitle" v-model="form.subtitle" />
                    </div>
                </InputGroup>
                <InputGroup>
                    <InputLabel hints="Leave blank to disable"> Footer Text </InputLabel>
                    <div class="w-full">
                        <Textarea placeholder="Footer Text (Ex: Powered by FormyChat)" class="resize-none"
                            name="form_footer" v-model="form.footer" />
                    </div>
                </InputGroup>
                <InputGroup :condition="form.mode == 'formychat'">
                    <InputLabel> Submit Button Text </InputLabel>
                    <Text placeholder="Button Text (Ex: Send on WhatsApp)" name="form_submit" v-model="form.submit" />
                </InputGroup>
                <InputGroup class="space-y-3" :condition="form.mode == 'formychat'">
                    <div class="flex items-center gap-3">
                        <Toggle v-model="form.show_country_code_field"
                            hints="This will add country code with number to choose." locked> Allow users to select
                            country code</Toggle>
                        <NewBadge />
                    </div>
                    <transition name="slide">
                        <div class="flex items-center gap-2" v-if="form.show_country_code_field">
                            <InputLabel> Default Country Code </InputLabel>
                            <Select v-model="form.country_code" class="w-fit" :options="$widget.contents.countries"
                                placeholder="Select country" />
                        </div>
                    </transition>
                </InputGroup>
                <InputGroup>
                    <InputLabel locked> Form Style </InputLabel>
                    <div class="flex items-center gap-4"
                        :class="{ 'opacity-60 formychat-locked child:pointer-events-none': isLocked }">
                        <ColorPicker v-model="form.text_color">Text Color</ColorPicker>
                        <ColorPicker v-model="form.background_color">Background</ColorPicker>
                    </div>
                </InputGroup>
                <InputGroup class="w-full">
                    <InputLabel>Font Family</InputLabel>
                    <Select :options="$widget.contents.fonts" v-model="form.font_family" placeholder="Select font"
                        class="w-56" />
                </InputGroup>
            </Group>
            <!-- Divider  -->
            <div class="border-b-2 border-dashed border-gray-200 rounded"></div>
            <!-- Form behavior  -->
            <Group title="Form Behavior" :gap="false">
                <InputGroup class="space-y-3">
                    <Toggle v-model="form.open_by_default" hints="Open the form by default when the page is loaded.">
                        Open by default</Toggle>
                    <Toggle v-model="form.close_on_submit" hints="Close the form after a successful submission.">Close
                        on Submit</Toggle>
                </InputGroup>
            </Group>
        </div>
        <!-- YouTube Video  -->
        <transition name="fade">
            <div v-if="YouTubeId" class="absolute left-0 top-0 w-full flex h-full z-20 items-center justify-center">
                <div class="absolute top-0 left-0 w-full h-full bg-black opacity-50" @click="YouTubeId = ''"></div>
                <div class="fixed top-1/2 -translate-y-1/2 w-full max-w-xl h-96 z-50">
                    <!-- close  -->
                    <div @click="YouTubeId = ''"
                        class="absolute -top-10 -right-10 cursor-pointer w-10 h-10 flex items-center justify-center text-white hover:brightness-110 hover:scale-110 transition">
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor"
                            class="h-5 w-5 fill-current">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                                d="M6 18L18 6M6 6l12 12"></path>
                        </svg>
                    </div>
                    <iframe class="w-full h-full"
                        :src="'https://www.youtube.com/embed/' + YouTubeId + '?autoplay=1'"></iframe>
                </div>
            </div>
        </transition>
    </div>
</template>
