<script setup>
import { computed, onMounted } from 'vue';
import { useWidgets } from '@store/widgets';
import { Select, Text, Button, IconUploader, Tooltip, Toggle, InputGroup, InputLabel, ProBadge } from '@components';

const $widget = useWidgets();
const whatsapp = computed(() => $widget.currentWidget?.config?.whatsapp || {})
const isLocked = !window.formychat_admin_vars.is_premium || false

// Use modal store.
import { useModal } from '@store/modal'
const $modal = useModal();

const resetWhatsAppError = () => {
    $widget.errors.whatsapp_number = false;
}

// Agent 
const addAgent = () => {
    whatsapp.value.agents = whatsapp.value.agents || [];
    whatsapp.value.agents.push({ 
        name: 'Agent ' + (whatsapp.value.agents.length + 1), 
        subtitle: '', 
        number: '', 
        country_code: window.formychat_country_code || '44' });
    $widget.errors.whatsapp_agents = false;
}

const deleteAgent = async (index) => {
    // Use modal to confirm deletion
    const confirmDelete = await $modal.prompt({
        title: 'Delete Agent',
        message: 'Are you sure you want to delete this agent?',
    });

    if (confirmDelete.ok) {
        whatsapp.value.agents.splice(index, 1);
    }
}

const moveAgent = (index, direction) => {
    const agents = whatsapp.value.agents;
    const newIndex = direction === 'up' ? index - 1 : index + 1;

    if (newIndex < 0 || newIndex >= agents.length) {
        return;
    }

    const agent = agents[index];
    agents.splice(index, 1);
    agents.splice(newIndex, 0, agent);
}


const initCountryCode = () => {
    if (!whatsapp.value.country_code || whatsapp.value.country_code.length === 0) {
        $widget.setCountryCode(window.formychat_country_code.toString() || '44');
    }
}
// on onMounted.
onMounted(async () => {
    // Set default agent mode
    whatsapp.value.agent_mode = whatsapp.value.agent_mode || 'single';

    // Set up event listener first
    window.addEventListener('formychat_ip_loaded', initCountryCode);

    setTimeout(() => {
        initCountryCode();
    }, 300);

});
</script>
<template>
    <div class="w-full h-full flex flex-col gap-8">
        <!-- Name  -->
        <InputGroup>
            <InputLabel>Widget name</InputLabel>
            <Text v-model="$widget.currentWidget.name" placeholder="Widget name" />
        </InputGroup>
        <!-- Agents  -->
        <InputGroup>
            <InputLabel>Choose how you prefer to display your widget</InputLabel>
            <div class="flex items-center gap-5">
                <div v-for="(agentLabel, agent) in { single: 'Single', agents: 'Multi-agents' }" :key="agent" :class="{
                    'ring-1 ring-slate-200 hover:bg-white bg-gray-50': whatsapp.agent_mode !== agent,
                    'ring-[2px] ring-indigo-400 ': whatsapp.agent_mode == agent,
                    'formychat-locked': isLocked && agent !== 'single'
                }" @click.prevent="whatsapp.agent_mode = isLocked ? 'single' : agent"
                    class="group relative w-40 h-40 ring-inset rounded cursor-pointer transition flex items-center justify-center flex-col gap-2">
                    <svg xmlns="http://www.w3.org/2000/svg" class="fill-current w-12" viewBox="0 0 16 16">
                        <path v-if="agent === 'single'"
                            d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6" />
                        <path v-else
                            d="M7 14s-1 0-1-1 1-4 5-4 5 3 5 4-1 1-1 1zm4-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6m-5.784 6A2.24 2.24 0 0 1 5 13c0-1.355.68-2.75 1.936-3.72A6.3 6.3 0 0 0 5 9c-4 0-5 3-5 4s1 1 1 1zM4.5 8a2.5 2.5 0 1 0 0-5 2.5 2.5 0 0 0 0 5" />
                    </svg>
                    <transition name="bounce">
                        <svg v-if="whatsapp.agent_mode == agent" xmlns="http://www.w3.org/2000/svg"
                            class="fill-indigo-500 w-4 absolute right-2 top-2" viewBox="0 0 16 16">
                            <path
                                d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
                        </svg>
                    </transition>
                    <!-- Locked. -->
                    <div v-if="isLocked && agent !== 'single'"
                        class="group-hover:opacity-100 transition duration-100 opacity-0 absolute inset-0 bg-white bg-opacity-50 rounded flex items-center justify-center">
                        <ProBadge />
                    </div>
                    <span class="font-medium">{{ agentLabel }}</span>
                </div>
            </div>
        </InputGroup>
        <!-- Single WhatsApp Number  -->
        <InputGroup v-if="whatsapp.agent_mode === 'single'">
            <InputLabel>WhatsApp Number</InputLabel>
            <div class="flex items-center gap-3">
                <Select :options="$widget.contents.countries" v-model="whatsapp.country_code" :cursor="false"
                    class="w-24 justify-center text-center" :placeholder="'Select'" />
                <div class="w-full relative">
                    <Text name="whatsapp_number" v-model="whatsapp.number" @keydown="resetWhatsAppError" type="text"
                        placeholder="WhatsApp number"
                        :error="!$widget.isValidPhone(whatsapp.number) || $widget.errors.whatsapp_number" />
                    <transition name="bounce">
                        <div v-if="!$widget.isValidPhone(whatsapp.number) || $widget.errors.whatsapp_number"
                            class="absolute w-4 text-red-500 right-3 top-3 flex items-center gap-2 transition"
                            :class="{ 'bounce-enter-active': $widget.errors.whatsapp_number }">
                            <svg xmlns="http://www.w3.org/2000/svg" class="fill-current" viewBox="0 0 16 16">
                                <path
                                    d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2" />
                            </svg>
                        </div>
                    </transition>
                </div>
            </div>
        </InputGroup>
        <!-- When Agent Mode is ON  -->
        <div class="flex flex-col gap-4" v-if="whatsapp.agent_mode === 'agents'">
            <!-- Agents  -->
            <div v-if="whatsapp.agents && whatsapp.agents.length" v-for="(agent, index) in whatsapp.agents" :key="index"
                class="flex flex-col gap-4">
                <div class="bg-gray-50 p-3 rounded border border-gray-100 flex flex-col gap-4 relative">
                    <!-- delete  -->
                    <div class="absolute z-10 right-3 top-3 child:cursor-pointer flex items-center gap-3">
                        <Tooltip v-if="index > 0">
                            <svg xmlns="http://www.w3.org/2000/svg" @click.prevent="moveAgent(index, 'up')"
                                class="fill-current rotate-180 w-3 opacity-60 hover:opacity-100 transition"
                                viewBox="0 0 16 16">
                                <path fill-rule="evenodd"
                                    d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708" />
                            </svg>
                            <template #content> Move up </template>
                        </Tooltip>
                        <Tooltip v-if="index < whatsapp.agents.length - 1">
                            <svg xmlns="http://www.w3.org/2000/svg" @click.prevent="moveAgent(index, 'down')"
                                class="fill-current w-3 opacity-60 hover:opacity-100 transition" viewBox="0 0 16 16">
                                <path fill-rule="evenodd"
                                    d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708" />
                            </svg>
                            <template #content> Move down </template>
                        </Tooltip>
                        <svg @click="deleteAgent(index)" xmlns="http://www.w3.org/2000/svg"
                            class="fill-current w-4 hover:opacity-100 transition hover:text-red-500"
                            viewBox="0 0 16 16">
                            <path
                                d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5M8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5m3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0" />
                        </svg>
                    </div>
                    <!-- Profile picture. -->
                    <InputGroup>
                        <IconUploader v-model="agent.avatar" v-model:custom="agent.custom_avatar"
                            :default="Image('whatsapp-circle.svg')" />
                    </InputGroup>
                    <!-- Name  -->
                    <InputGroup>
                        <InputLabel> Agent name </InputLabel>
                        <Text placeholder="Example: John Doe" v-model="agent.name" />
                    </InputGroup>
                    <!-- Subtitle  -->
                    <InputGroup>
                        <InputLabel> Subtitle / Designation </InputLabel>
                        <Text placeholder="Example: Technical Support Executive" v-model="agent.subtitle" />
                    </InputGroup>
                    <!-- WhatsApp Number  -->
                    <InputGroup>
                        <InputLabel> Agent WhatsApp number </InputLabel>
                        <div class="flex items-center gap-3">
                            <Select :options="$widget.contents.countries" v-model="agent.country_code" :cursor="false"
                                class="w-24 justify-center text-center" :placeholder="'Select'" />
                            <div class="w-full relative">
                                <Text v-model="agent.number" type="text"
                                @keydown="$widget.errors['agent_number_'+index] = false" :error="!$widget.isValidPhone(agent.number) || ($widget.errors['agent_number_'+index])" 
                                placeholder="WhatsApp number" :name="'agent_number_' + index"/>
                                <transition name="bounce">
                                    <div v-if="$widget.errors['agent_number_' + index]"
                                        class="absolute w-4 text-red-500 right-3 top-3 flex items-center gap-2 transition"
                                        :class="{ 'bounce-enter-active': $widget.errors.whatsapp_number }">
                                        <svg xmlns="http://www.w3.org/2000/svg" class="fill-current"
                                            viewBox="0 0 16 16">
                                            <path
                                                d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2" />
                                        </svg>
                                    </div>
                                </transition>
                            </div>
                        </div>
                    </InputGroup>
                </div>
            </div>
            <!-- No agent found  -->
            <!-- <div v-else class="bg-indigo-50 border border-indigo-200 px-6 py-4 rounded text-indigo-500">Create your first agent</div> -->
            <!-- Add agent  -->
            <!-- When no agents  -->
            <transition name="fade">
                <div v-if="$widget.errors.whatsapp_agents"
                    class="text-red-500 text-sm py-4 flex items-center justify-between px-6 rounded bg-red-50 border border-red-200">
                    Please add at least one agent <span class="cursor-pointer px-3 py-1 bg-red-300 rounded"
                        @click="$widget.errors.whatsapp_agents = false">x</span>
                </div>
            </transition>
            <div> <Button outline class="px-2 h-8" @click="addAgent"><svg xmlns="http://www.w3.org/2000/svg" width="14"
                        height="14" fill="currentColor" class="bi bi-plus-lg" viewBox="0 0 16 16">
                        <path fill-rule="evenodd"
                            d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2" />
                    </svg> Add new agent</Button> </div>
        </div>
        <!-- Redirect to whatsapp on clicking agent  -->
        <div class="flex items-start gap-8 flex-col">
            <h3 class="font-semibold">Behaviors</h3>
            <Toggle v-model="whatsapp.new_tab"
                hints="Open the WhatsApp link in a new tab on Desktop. <br/><b>Note:</b> In Safari, WhatsApp will be opened in current tab.">
                Open WhatsApp in a new tab</Toggle>
            <Toggle v-model="whatsapp.web_version" locked hints="By enabling this feature, WhatsApp will be automatically opened on your web
            browser when accessed from a laptop/PC.">Navigate to WhatsApp Web from desktop</Toggle>
            <div v-if="whatsapp.agent_mode === 'agents'" class="flex flex-col gap-3">
                <InputLabel>On click Agent</InputLabel>
                <div class="flex flex-col gap-2.5 child:cursor-pointer">
                    <label class="flex items-center gap-4 group"
                        v-for="(label, action) in { show_form: 'Load The Selected Form', redirect: 'Redirected to WhatsApp directly' }"
                        @click.prevent="(isLocked && action == 'redirect') ? null : whatsapp.on_click_agent = action"
                        :class="{ 'text-indigo-500': whatsapp.on_click_agent == action, }">
                        <div class="w-5 h-5 bg-white border-[5px] rounded-full  transition" :class="{
                            'border-indigo-500': whatsapp.on_click_agent == action,
                            'border-gray-100': whatsapp.on_click_agent !== action,
                            'group-hover:border-indigo-500': whatsapp.on_click_agent !== action && !isLocked,
                            'formychat-locked': isLocked && action === 'redirect'
                        }"><span></span></div> <span :class="{
                            'opacity-60 formychat-locked': isLocked && action === 'redirect'
                        }">{{ label }}</span>
                        <ProBadge v-if="action === 'redirect'" />
                    </label>
                </div>
            </div>
        </div>
    </div>
</template>